.section__buttons {
  padding-top: 50px;
  grid-row: 1 / 2;
  grid-column: 1 / -1; }

.section__skills {
  grid-row: 2 / 3;
  grid-column: 1 / -1;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 10px; }

.section__image {
  grid-row: 3 / -1;
  grid-column: 1 / 2; }

.section__links {
  grid-row: 3 / -1;
  grid-column: 2 / 4; }

.section__bio {
  grid-row: 3 / -1;
  grid-column: 4 / -1; }

.control__button {
  border: 2px solid #3e5a71;
  border-radius: 10px;
  padding: 10px 10px 10px 10px;
  cursor: pointer;
  color: #263745;
  background: #eff8ff;
  font-size: 1em;
  font-weight: 500;
  width: 100px;
  margin-right: 50px; }
